@mixin MD {
  // Navbar {
  #navbar {
    width: 35rem;
  }

  // showcase
  #showcase {
    background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)),
      url(../img/products/Acer_Predator_21X-3.JPG) no-repeat center center/cover;

    .showcase {
      &__title {
        font-size: 4.25rem;
      }
      &__text {
        font-size: 2rem;

        &--sub {
          .showcase__price {
            font-size: 3rem;
            color: color(primary);
          }
        }
      }
    }
  }

  // Section - Cards
  #cards {
    .cards {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 350px);
      grid-row-gap: 5rem;
      grid-template-areas:
        'card-1 card-2'
        'card-3 card-3';

      .card {
        &--1 {
          grid-area: card-1;
        }
        &--2 {
          grid-area: card-2;
        }
        &--3 {
          grid-area: card-3;
        }
      }
    }
  }

  // Section - Collection - No Changes

  //Section - New Arrivals
  #arrival {
    .arrival {
      flex-direction: column;
      &__item {
        padding-right: 10%;
        &__text {
          font-size: 2rem;
        }
        &--2 {
          background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.5)),
            url(../img/products/Acer_Predator_21X-2.JPG) no-repeat center
              center/cover;
          align-items: flex-end;
        }
      }
    }
  }

  // Section - Promo Video
  #promo {
    .promo__content {
      top: 50%;
      left: 10%;
      right: 10%;
      transform: translate(0, -50%);
      .promo__title {
        @include title(0.5rem, 0, 3.5rem);
      }
    }
  }

  // Section - Links
  #links {
    .links {
      grid-column-gap: 1.5rem;
    }
  }
}

